import axios from 'axios'
import store from '@/store'
import storage from 'store'
import { VueAxios } from './axios'
import { ACCESS_TOKEN } from '@/store/mutation-types'
import userApi from '@/api/wei-index/userApi'
import router from '@/router'

// 创建 axios 实例
const request = axios.create({
  // API 请求的默认前缀
  baseURL: process.env.BASE_API,
  timeout: 0 // 请求超时时间
})

// 异常拦截处理器
const errorHandler = error => {
  console.log('进入异常处理')
  if (error.response) {
    const data = error.response.data
    // 从 localstorage 获取 token
    const token = storage.get(ACCESS_TOKEN)
    if (error.response.status === 403) {
    }
    if (
      error.response.status === 401 &&
      !(data.result && data.result.isLogin)
    ) {
      if (token) {
        store.dispatch('Logout').then(() => {
          setTimeout(() => {
            window.location.reload()
          }, 1500)
        })
      }
    }
  }
  return Promise.reject(error)
}

// 是否正在刷新 token 的标记 -- 防止重复发出刷新token接口
let isRefreshing = false

// request interceptor, 请求拦截器
request.interceptors.request.use(config => {
  console.log('请求拦截器', config)
  const token = storage.get(ACCESS_TOKEN)
  // 如果 token 存在
  // 让每个请求携带自定义 token 请根据实际情况自行修改
  if (token) {
    config.headers = {
      ...config.headers,
      token: isRefreshing ? storage.get('refreshToken') : token
    }
  }
  return config
}, errorHandler)

// 失效后同时发送请求的容器 -- 缓存接口
let subscribers = []

// 刷新 token 后, 将缓存的接口重新请求一次
function onAccessTokenFetched(newToken) {
  subscribers.forEach(callback => {
    callback(newToken)
  })
  // 清空缓存接口
  subscribers = []
}

// 添加缓存接口
function addSubscriber(callback) {
  subscribers.push(callback)
}

// 响应拦截器
request.interceptors.response.use(response => {
  console.log('响应拦截器', response)
  // 当response.data.re为401, 则判断token已经过期
  // /access_token/refresh 为刷新token的接口, 需要排除掉
  if (
    !response.config.url.includes('/access_token/refresh') &&
    response.data.code === 401
  ) {
    // token已过期，不再刷新
    let expireTime = storage.get('expireTime')
    if (new Date(expireTime) - new Date() <= 0) {
      router.replace({ name: 'login' })
      return response.data
    }
    if (!isRefreshing) {
      isRefreshing = true
      userApi
        .refreshToken()
        .then(res => {
          console.log('token刷新结果：', res)
          if (res.code === 200) {
            let expireTime = new Date(
              new Date().getTime() + res.data.expireTime * 60 * 1000
            )
            storage.set('accessToken', res.data.accessToken)
            storage.set('refreshToken', res.data.refreshToken)
            storage.set('expireTime', expireTime)
            // 当刷新成功后, 重新发送缓存请求
            onAccessTokenFetched(res.data.accessToken)
          } else {
            storage.removeItem('accessToken')
            storage.removeItem('refreshToken')
            storage.removeItem('expireTime')
            this.$message.error('登录过期')
          }
        })
        .catch(() => {
          storage.removeItem('accessToken')
          storage.removeItem('refreshToken')
          storage.removeItem('expireTime')
          this.$message.error('登录过期')
        })
        .finally(() => {
          isRefreshing = false
        })
      // 将其他接口缓存起来 -- 这个Promise函数很关键
      const retryOriginalRequest = new Promise(resolve => {
        // 这里是将其他接口缓存起来的关键, 返回Promise并且让其状态一直为等待状态,
        // 只有当token刷新成功后, 就会调用通过addSubscriber函数添加的缓存接口,
        // 此时, Promise的状态就会变成resolve
        addSubscriber(newToken => {
          // 表示用新的token去替换掉原来的token
          response.config.headers.token = newToken
          // 替换掉url -- 因为baseURL会扩展请求url
          response.config.url = response.config.url.replace(
            response.config.baseURL,
            ''
          )
          // 用重新封装的config去请求, 就会将重新请求后的返回
          resolve(request(response.config))
        })
      })
      return retryOriginalRequest
    }
  }
  return response.data
}, errorHandler)
const installer = {
  vm: {},
  install(Vue) {
    Vue.use(VueAxios, request)
  }
}

export default request

export { installer as VueAxios, request as axios }



