import Vue from 'vue'
import Router from 'vue-router'
// import HelloWorld from '@/components/HelloWorld'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'index',
      component: () => import('@/views/WeIndex')
    },
    {
      path: '/login',
      name: 'login',
      component: () => import('@/views/common/login')
    },
    {
      path: '/404',
      name: '404',
      component: () => import('@/views/common/404')
    },
    {
      path: '/about-us',
      name: 'aboutus',
      component: () => import('@/views/common/AboutUs')
    },
    {
      path: '/chatroom',
      name: 'chatroom',
      component: () => import('@/views/chat')
    }
  ]
})

