import request from '@/utils/request'

const userApi = {
  baseUri: '/user'
}

export function login(parameter) {
  return request({
    url: userApi.baseUri + '/login',
    method: 'post',
    data: parameter
  })
}
export function register(parameter) {
  return request({
    url: userApi.baseUri + '/register',
    method: 'post',
    data: parameter
  })
}
export function refreshToken(parameter) {
  return request({
    url: userApi.baseUri + '/access_token/refresh',
    method: 'get',
    data: parameter
  })
}



