import request from '@/utils/request'

const userApi = {
  baseUri: '/user',
  login: function (parameter) {
    return request({
      url: this.baseUri + '/login',
      method: 'post',
      data: parameter
    })
  },
  register: function (parameter) {
    return request({
      url: this.baseUri + '/register',
      method: 'post',
      data: parameter
    })
  },
  refreshToken: function () {
    return request({
      url: this.baseUri + '/access_token/refresh',
      method: 'get'
    })
  }
}

export default userApi



